.checkbox {
  display: block;
  position: relative;
  padding-left: 1.375rem;
  cursor: pointer;
  user-select: none; }
  @media (max-width: 767px) {
    .checkbox {
      font-size: 0.875rem; } }
  .checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0; }
  .checkbox .checkmark {
    position: absolute;
    top: 0.25rem;
    left: 0;
    height: 1rem;
    width: 1rem;
    background-color: transparent;
    border: 2px solid #000000;
    border-radius: 3px; }
  .checkbox:hover input ~ .checkmark {
    background-color: #DEE2E6; }
  .checkbox .checkmark:after {
    content: "";
    position: absolute;
    display: none; }
  .checkbox input:checked ~ .checkmark:after {
    display: block; }
  .checkbox .checkmark:after {
    left: 4px;
    top: 0;
    width: 5px;
    height: 10px;
    border: solid #000000;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg); }

